// rem
// 
const baseSize = 16
//  rem 
function setRem(delay: boolean) {
  //  1920
  const scale = document.documentElement.clientWidth / 1920
  // Math.min(scale, 2) 2
  const fontSize = baseSize * Math.min(scale, 2) + 'px';
  if (!delay) {
    document.documentElement.style.fontSize = fontSize;
  } else {
    setTimeout(() => {
      document.documentElement.style.fontSize = fontSize;
    }, 300);
  }

}
// 
setRem(false);
//  rem
window.onresize = function () {
  setRem(true)
}