import { observer } from 'mobx-react';
import { researchStore } from '../../stores/ResearchStore';
import './Main.scss';
import { ResearchItem } from '../../services/ResearchService';
import ResearchItemPage from '../Research/ResearchItem';
import { useHistory } from 'react-router';
import { Index_Bg_Img } from '../../images/IndexBg';
import { Index_News_Img } from '../../images/IndexNews';
import { Index_Market_Img } from '../../images/IndexMarket';
import { Focus_News_Img } from '../../images/FocusNews';
import { List_Bg_Img } from '../../images/ListBg';
import { Focus_Research_Img } from '../../images/FocusResearch';
import { Focus_Market_Img } from '../../images/FocusMarket';
import Arrow_More_White_Img from '../../images/icons/arrow-right.svg';
import { userStore } from '../../stores/UserStore';
import { Main_Img } from '../../images/main/main';
import { Main1_Img } from '../../images/main/main1';
import { Main2_Img } from '../../images/main/main2';
import { Main3_Img } from '../../images/main/main3';
import { Main4_Img } from '../../images/main/main4';
import { useEffect } from 'react';
import { mainStore } from '../../stores/MainStore';
import { goto } from '../../utils/globalFuncs';
import { Qr_Android_Img } from '../../images/QrAndroid';
import { Qr_Ios_Img } from '../../images/QrIos';
import { Ios_Icon } from '../../images/IosIcon';
import { Android_Icon } from '../../images/AndroidIcon';

export const Main = observer(() => {
  let history = useHistory();
  useEffect(() => {
    mainStore.init();
  }, [])
  return (
    <div className="mainWrapper">
      <div className="top"  >
        <img src={Index_Bg_Img} alt="" className="bg" />
        <div className="info">
          <div className="topLeft">
            <h1 className="topTitle">ANALYSE SMART</h1>
            <h1 className="subTitle">DREAM BIG.</h1>
            <div className="guide">
              <img src={Index_News_Img} alt="" className="icon" />
              <div className="abstract">
                <div className="title">Oli </div>
                <div className="details">. </div>

              </div>
            </div>
            <div className="guide">
              <img src={Index_Market_Img} alt="" className="icon" />
              <div className="abstract">
                <div className="title">Oli </div>
                <div className="details">. </div>
              </div>
            </div>
            <div className="qrcodes">
            <div className="code">
           {mainStore.qrcode == 'android' && <img className="qrcode" src={Qr_Android_Img} alt="" />}
           {mainStore.qrcode == 'ios' && <img className="qrcode" src={Qr_Ios_Img} alt="" />}
            </div>
            <div className="btns">
              <div className="btn" onClick={()=>{
                mainStore.qrcode = 'ios'
              }}>
                <div><img className="icon" src={Ios_Icon} alt="" /></div>
                <div>IOS</div>
              </div>
              <div className="btn" onClick={()=>{
                mainStore.qrcode = 'android'
              }}>
                <div><img className="icon"  src={Android_Icon} alt="" /></div>
                <div>Android</div>
              </div>
            </div>
          </div>
          </div >
         
          <div className="images">
            <img src={Main_Img} alt="" />
            <img className="swiper" style={{ zIndex: mainStore.swiperIndex == 0 ? 1 : -1 }} src={Main1_Img} alt="" />
            <img className="swiper" style={{ zIndex: mainStore.swiperIndex == 1 ? 1 : -1 }} src={Main2_Img} alt="" />
            <img className="swiper" style={{ zIndex: mainStore.swiperIndex == 2 ? 1 : -1 }} src={Main3_Img} alt="" />
            <img className="swiper" style={{ zIndex: mainStore.swiperIndex == 3 ? 1 : -1 }} src={Main4_Img} alt="" />
          </div>
        
        </div>
        <div className="dots">
            {[0,1,2,3].map((i)=><div onClick={()=>{
              mainStore.swiperIndex = i;
            }} className={mainStore.swiperIndex ==i?"active dot" : "dot"}></div>  )}
          </div>
      </div>
      <div className="focus">
        <div className="area">
          Focus Area
          <span>.</span>
        </div>
        <div className="items">
          <div className="item">
            <img src={Focus_News_Img} alt="" className="icon" />
            <div className="title">
              Oli 
            </div>
            <div className="content">
              .
            </div>
            <div className="more" onClick={() => {
              window.scrollTo(0, 0)
              history.push("/news")
            }}>
              <div>
                Read More
              </div>
            </div>
          </div>
          <div className="item">
            <img src={Focus_Research_Img} alt="" className="icon" />
            <div className="title">
              Oli 
            </div>
            <div className="content">
              ..
            </div>
            <div className="more" onClick={() => {
                           goto("/research");

              
            }}>
              <div>
                Read More
              </div>
            </div>
          </div>
          <div className="item">
            <img src={Focus_Market_Img} alt="" className="icon" />
            <div className="title">
              Oli 
            </div>
            <div className="content">
              ..
            </div>


            <div className="more" onClick={() => {
              window.scrollTo(0, 0)
              history.push("/watchList")
            }}>
              <div>
                Read More
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="researchWrapper" style={{ backgroundImage: `url('${List_Bg_Img}')` }} >
        <h2>Latest Researches<span>.</span></h2>
        <p>.</p>
        <div className="list">
          {researchStore.latestList.slice(0, 3).map((item: ResearchItem, i) => <ResearchItemPage idx={i} item={item} key={i} />
          )}
        </div>
        <div className="all" onClick={() => {
          goto("/research");
        }}>
          VIEW ALL
          <img src={Arrow_More_White_Img} alt="" />
        </div>
      </div>
    </div>
  );
})


